    .footer {
        font-family: Arial, Helvetica, sans-serif;
        margin: 10px 0 0 0;
        background-color: #FFFFFF;
        padding: 20px 0px;
        &__up {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            width: 100%;
            font-size: 14px;
            @media screen and (max-width: 1160px) {
                width: 100%;
            }
            @media screen and (max-width:1050px) {
                margin: 0 30px;
                width: 100%;
            }
            @media screen and (max-width:950px) {
                margin: 0 20px;
                font-size: 12px;
            }
            @media screen and (max-width:840px) {
                margin: 0 10px;
                width: 100%;
            }
            @media screen and (max-width:790px) {
                margin: 0 5px;
                font-size: 9.5px;
                width: 100%;
            }
            @media screen and (max-width:600px) {
                flex-direction: column;
                align-items: center;
            }
            &__element {
                line-height: 25px;
                display: flex;
                padding: 0px 30px;
                flex-direction: column;
                @media screen and (max-width: 600px) {
                    margin-top: 10px;
                    line-height: 20px;
                }
                span,
                a {
                    cursor: pointer;
                    text-decoration: none;
                    color: #2b73cc;
                    transition: all 0.3s;
                    &:hover {
                        color: #666;
                    }
                }
            }
        }
        &__down {
            background-color: #FFF;
            color: #999999;
            font-family: Arial, Helvetica, sans-serif;
            margin: 10px 0;
            overflow: hidden;
            p {
                font-size: 10px;
                line-height: 18px;
                padding: 0 74px;
                text-align: start;
                @media screen and (max-width: 600px) {
                    padding: 0 5px;
                    font-size: 8px;
                }
            }
        }
    }
    
    .social-links {
        display: flex;
        width: 100%;
        justify-content: space-around;
    }
    
    .logo-links {
        display: flex;
        flex-direction: column;
        a {
            padding-bottom: 10px;
        }
    }